.container {
  font-family: monospace;
  margin: 0 auto;
  padding-left: 50px;
}

.title,
.statisticsTitle {
  font-size: 30px;
  font-weight: bold;
}
.statisticsValue,
.noFeedback {
  font-size: 18px;
}
.feedbackBtn:not(:last-of-type) {
  margin-right: 10px;
}

.feedbackBtn {
  background-color: transparent;
  border: none;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  outline: none;
}

.feedbackBtn:active {
  background-color: teal;
}
